.wrapper__input {
  --border-radius: 3px;
  --border: 3px;
  --clear-width: 35px;
}

.wrapper__input {
  position: relative;
  display: inline-flex;
}

.input {
  min-width: 300px;
  padding: 10px 15px;
  padding-right: calc(var(--clear-width) + 15px);

  border: var(--border) solid #c2c2c2;
  border-radius: var(--border-radius);
  font-size: 16px;
  font-family: sans-serif;
  outline: none;
}

.input:focus {
  border: var(--border) solid #6a83fb;
}

.clear {
  position: absolute;

  top: var(--border);
  right: var(--border);
  width: var(--clear-width);
  height: calc(100% - var(--border) * 2);

  padding: 10px;
  object-fit: contain;
  object-position: center center;
  transition: .5s;
  opacity: .4;
  cursor: pointer;
}

.clear__disabled {
  opacity: .2;
}